div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 99%;
  margin:0 auto;
  background-color: rgb(246, 245, 245);
}

header{
  flex-basis: 30vh;
  background-color: rgb(245, 243, 243);
  color:black;
}

main{
  display: flex;
  flex-grow: 1;
  
  color:black;
  padding: 10px 0;

}

aside{
  min-width: 20vw;
  background-color: rgb(220, 245, 245);
  padding:10px;
}

section{
  flex-grow: 1;
  background-color: rgb(252, 245, 237);
}

footer{
  background-color:slategray ;
  color:yellow;
  padding:20px;
}

header{
    position: relative;
    overflow: hidden;
}

header img {
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    min-width: 100%;
    min-height: 100%;
  
}
  .navigation{
    display: flex;
    flex-direction: row;
  }
  
  
  .navul{
    list-style: none;
    }
  
  .navli{
    flex-grow: 1;
    margin-top:15px;
  }
  
  .navigation a{
    display: block;
    text-align: left;
    color:white;
    padding: 15px 25px;
    text-decoration: none;
    text-transform: uppercase;
    border-left: 10px solid  darkred;
    background-color: darkred;
  }
  
  .navigation a:hover{
    color:yellow;
    -webkit-animation: App-logo-spin infinite 1s linear;
            animation: App-logo-spin infinite 1s linear;
  }
  
  section{
    padding-top:40px;
  }
  
  .active{
    
  }

  .navigation .home_selected{
    border-left-color: yellow;
  }
  
  .navigation .products_selected{
    border-left-color: red;
  
  }
  
  .navigation .contact_selected {
    border-left-color: green;
  }

  .navigation .admin_selected {
    border-left-color: blue;
  }

  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
    to {
      -webkit-transform: translateX(10px);
              transform: translateX(10px);
    }
  }

  @keyframes App-logo-spin {
    from {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
    to {
      -webkit-transform: translateX(10px);
              transform: translateX(10px);
    }
  }
  
.article_component{
    padding: 0 10px;
    text-align: justify;
}

.article_component h1{
    text-align: center;
    font-size: 30px;
    padding-top: 40px;
    text-transform: uppercase; 
}

.article_component h2{
    text-align: center;
    font-size: 20px;
    text-transform: uppercase; 
    padding: 40px 0 10px 0;
}

.article_component h4{
    text-align: center;
    padding-bottom: 20px;
}

.article_component p{
    font-size: 18px;
}


.products {
    font-size: 20px;
}
.products ul {
    list-style:square;
}

.products li {
    margin: 20px 0;
}

.products a {
    text-decoration: none;
}

.products a:hover{
    text-decoration: underline;
    -webkit-text-decoration-color: red;
            text-decoration-color: red;
}
.contact {
    display: block;
}

.contact textarea{
    display: block;
    font-size: 20px;
    width: 80%;
    min-height: 200px;
    line-height: 1.3;
    margin: 0 auto;
}

.contact button{
    display: block;
    padding: 10px 30px;
    margin: 20px 10% 0 auto;
    cursor: pointer;
    background-color: brown;
    color: yellow;
    -webkit-transition: 2s;
    transition: 2s;
}

.contact button:hover{
    background-color: yellow;
    color: brown;
}

.contact h1{
    display: block;
    font-size: 26px;
    margin: 0 auto 20px 10%;

}
.loginpage {
    display: block;
    width: 80%;
    margin: 0 auto;
}

.loginpage label{
    margin:10px 0 0 10px;
}

.loginpage button{
    display: block;
    margin:20px 72% 0 auto;
    padding: 5px 20px;
    background-color:aqua;
    cursor: pointer;
}

.loginpage input {
    margin-top: 10px;
}
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-family: 'Open+Sans';
  font-size: 12px;
}

